<template>
  <h1 class="title">Proyectos</h1>

  <p id="projects-section-text" class="section-text">
    Esta es la lista de todos los proyectos que he realizado 
    desde mis comienzos como desarrollador web. 

    Visita mi GitHub para ver los repositorios de cada proyecto.
  </p>

  <div id="projects" v-if="projects">
    <article
      class="project"
      @mouseover="infoHoverON(project._id)"
      @mouseleave="infoHoverOFF(project._id)"
      v-for="project in projects"
      :key="project._id"
    >
      <img
        :src="'http://localhost:3900/api/get-image/' + project.image"
        :alt="project.title"
      />

      <div :class="'project-info project-info-' + project._id">
        <div class="project-title">
          <h2>{{ project.title }}</h2>
        </div>

        <p>{{ project.description }}</p>

        <form :action="project.link" target="_blank">
          <button>Ir a la web</button>
        </form>
      </div>
    </article>
  </div>
</template>

<script>
import gsap from "gsap";
import axios from "axios";

export default {
  name: "Projects",
  data() {
    return {
      projects: [],
    };
  },
  setup() {
    function infoHoverON(_id) {
      let classID = ".project-info-" + _id;
      gsap.to(classID, {
        duration: 0.3,
        y: "-250px",
        background: "rgba(0, 0, 0, 0.9)",
      });
    }

    function infoHoverOFF(_id) {
      let classID = ".project-info-" + _id;
      gsap.to(classID, {
        duration: 0.3,
        y: "0%",
        background: "rgba(0, 0, 0, 0.5)",
      });
    }

    function entryAnimation(){

    gsap.fromTo(".project", {x: "-30px", opacity: 0}, {stagger: 0.2, x: "0px", opacity: 1})

      
    }

    function getProjects() {
      axios.get("http://localhost:3900/api/projects").then((res) => {
        if (res.data.status == "success") {
          this.projects = res.data.projects;
        }
      });
      for (var project in this.projects) {
        this.projects[project].className += project._id;
      }
    }

    return { infoHoverON, infoHoverOFF, entryAnimation, getProjects };
  },
  mounted() {
    this.getProjects();
  },
  updated() {
    this.entryAnimation();
  }
};
</script>